.Header {
  display: flex;
  align-items: center;
  height: 50px;
  background-color: #2d333b;

  .nav {
    display: flex;
    width: calc(100% - 30px);
    max-width: 1200px;
    margin: 0 auto;

    .nav__logo {
      width: 40px;
    }

    .nav__list {
      display: flex;
      margin: 0;
      padding: 0;
      list-style-type: none;
    }

    .nav__item {
      padding: 0 10px;
      border: 2px solid #2d333b;
      text-transform: uppercase;
    }

    .nav__item:first-of-type {
      padding-left: 0;
    }

    .nav__link {
      font: 400 18px/46px 'Roboto', sans-serif;
      color: #fff;
      text-decoration: none;
    }

    .nav__link.active {
      border-bottom: 2px solid #ec775c;
    }
  }
}
